import "@getyoti/react-face-capture/index.css";
import React from "react";
import ReactDOM from "react-dom";
import FaceCapture, {
  CAPTURE_METHOD,
  COUNTDOWN_MODES,
  ERROR_CODE,
  FORMAT_TYPE,
  IMAGE_TYPE,
  LANGUAGE_CODE,
  QUALITY_TYPE,
  RESOLUTION_TYPE,
} from "@getyoti/react-face-capture";

const render = (props, domRef) => {
  const mount = () => {
    ReactDOM.render(<FaceCapture {...props} />, domRef);
  };

  const unmount = () => {
    ReactDOM.unmountComponentAtNode(domRef);
  };

  const reload = () => {
    unmount();
    mount();
  };

  mount();

  return { reload, unmount };
};

window.Yoti = window.Yoti || {};
window.Yoti.FaceCaptureModule = {
  render,
  CAPTURE_METHOD,
  COUNTDOWN_MODES,
  ERROR_CODE,
  FORMAT_TYPE,
  IMAGE_TYPE,
  LANGUAGE_CODE,
  QUALITY_TYPE,
  RESOLUTION_TYPE,
};
